<template>
  <div class="top">
    <div class="info_wrapper">
      <div class="name">
        <span>姓名</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入姓名" v-model="name"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>联系电话</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入联系电话" v-model="phone"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>微信号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入微信号" v-model="weixin"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>身份证号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入身份证号" v-model="user_id"/>
    </div>
  
    <div class="info_wrapper" @click="show=true">
      <div class="name">
        <span>所在区域</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请选择区域" v-model="area"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>入驻类型</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <!-- <van-icon name="location" color="#15BDDB"/> -->
        <span style="color:#15BDDB;">总代理</span>
      </div>
    </div>



    <!-- 提交 -->
    <div class="submit">
      <button class="btn" @click="getsetin()">提交申请</button>
    </div>

    <!-- 入驻身份单选 -->
    <!-- <van-Popup  v-model="activeNames" id="shenfen">
      <strong class="title">请选择入驻类型</strong>
      <van-radio-group v-model="radio" @change="getshenfen()">
        <van-radio :name="index" v-for="(item,index) in shenfen_data" :key="index">{{item}}</van-radio>
      </van-radio-group>
    </van-Popup > -->

    <!-- 选择地址 -->
    <van-popup v-model="show" position="bottom">
      <van-area :area-list="site" @cancel="show=false" @confirm="changeAddress"/>
    </van-popup>

    <!-- 选择地址 -->
    <!-- <van-popup v-model="address" position="bottom" :style="{ height: '100%',width: '100%' }">
      <div class="search">
        <input type="text" placeholder="请输入搜索关键字" v-model="address_key" @blur="getaddress()" class="input">
        <div @click="getaddress()" class="se">搜索</div>
      </div>
      <div class="item" @click="address=false;getjwd(item,index)"
      v-for="(item,index) in address_data" 
      :key="index">
      <van-icon name="location" color="#15BDDB"/>{{item.district}}{{item.address}}{{item.name}}</div>
    </van-popup> -->
  </div>
</template>

<script>
import { getmap, setin } from '@/axios/getData'
import { Popup, Radio, Area, RadioGroup, Icon, Toast  } from 'vant';
import util from '../utils/utils.js'  
export default {
  data(){
    return{
      navradio:'1',   //  单选  选择自己或团队
      address_key:'',
      address_search:'请选择详细地址',
      address:false,      //  定位
      address_data:[],    //  地理位置数据
      jd:'',              //  经纬度
      wd:'',
      mapshow:false,
      name:'',            //  用户名
      phone:'',           //  手机电话号码
      shenfen:'',         //  入驻身份 
      weixin:'',          //  微信号
      user_id:'',         //  身份证
      area:'',            //  区域
      show:false,         //  选择地址
      // province:'',        //  省
      // city:'',            //  市
      // district:'',        //  区
      activeNames:false,
      radio:'',   //  身份选择  +3    对应3 4 5
      shenfen_data:['酒店', '分销商', '总代理'],   
      data:[],
      isshow:false,       // 
      value1: 0,
      value2: 'a',
      option1: [
        { text: '全部商品', value: 0 },
        { text: '新款商品', value: 1 },
        { text: '活动商品', value: 2 }
      ],
      taocan:'',
      site: util.site,
    }
  },
  components: {
    vanPopup: Popup,
    vanRadio: Radio,
    vanArea: Area,
    vanRadioGroup: RadioGroup,
    vanIcon: Icon
  },
  created() {
    // this.getdata()
  },
  methods:{
    // 数据提交
    getsetin(){
      if( this.name&& this.phone&& this.weixin&& this.user_id&& this.area){
        var data = {}
        data.username = this.name;
        data.mobile = this.phone;
        data.wx_number = this.weixin;
        data.code_id = this.user_id;
        data.region = this.area;
        setin(data).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.$router.go(-1)
          } else if(res.code==10000){
            Toast(res.msg);
            this.$router.go(-1)
          }
        })
      } else {
        Toast('请完善入驻信息')
      }
    },
    // 点击获取经纬度
    getjwd(item,i){
      this.jd = item.location.split(',')[0];
      this.wd = item.location.split(',')[1];
      this.address_search = item.district +'-'+item.address +'-'+item.name
    },
    // 获取详细地址  == 经纬度
    getaddress(){
      var  that = this
      if(!that.address_key){
        Toast('请输入搜索关键字');
        return;
      }
      axios.get("https://restapi.amap.com/v3/assistant/inputtips", {
        params: {
          key: "182a36b8ef5943532fa7d717f378e858",
          keywords: that.address_key
        }
      }).then(res => {
        // console.log(res)
        that.address_data = res.tips;
      });
    },
    // 获取身份
    getshenfen(){
      this.activeNames = false;
      this.shenfen = this.shenfen_data[this.radio]
    },
    // 变更地址
    changeAddress(val) {
      this.area = val[0].name +'-'+ val[1].name +'-'+ val[2].name
      this.show = false
    },
    saoma(){
      wx.ready(() => {
        wx.getLocation({
          type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
          success: function (res) {
            var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
            var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
            var speed = res.speed; // 速度，以米/每秒计
            var accuracy = res.accuracy; // 位置精度
            console.log(res)
          }
        });
      });
    },
    getdata(){
      var that = this
      var url = window.location.href
      getmap({url}).then(res => {
        console.log(res.data)
        let data = res.data;
        let op = {
          debug: false,
          appId: data.appId, //公众号的唯一标识
          timestamp: data.timestamp, // 生成签名的时间戳
          nonceStr: data.nonceStr, // 生成签名的随机串
          signature: data.signature, // 签名
          jsApiList: ["getLocation"] // 需要使用的JS接口列表(我只需要调用扫一扫的接口，如有多个接口用逗号分隔)
        };
        wx.config(op);
        that.saoma();
        wx.error(r => {

        });
      });
    },
  },
  mounted(){

    // window.addEventListener('message', function(event) {
    //   // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
    //   var loc = event.data;
    //   if (loc && loc.module == 'locationPicker') {//防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
    //     console.log('location', loc);
    //   }
    // }, false);
  }
}
</script>

<style scoped lang="scss">
.top{
  padding:50px 15%;
  height:100vh;
  background: #FAFAFA;
}
  .info_wrapper {
    padding: 25px 0;
    box-sizing:border-box;
    border-bottom: 1px solid rgb(238, 238, 238);
    display: flex;
    align-items: center;
    .name{
      width:25%;
      text-align: right;
    }
    .red {
      color: #e62121;
    }
    .weizhi{
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
      background: #fff;
    }
    input {
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
    }
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  #shenfen{
    width:50%;
    padding:50px;
    border-radius: 10px;
    .title{
      text-align: center;
      display: inline-block;
      width:100%;
      margin-bottom:20px;
    }
    /deep/ .van-radio{
      margin-bottom:15px;
    }
  }
  #ditu{
    width:101%;
    height:100%;
  }
  .search{
    display: flex;
    padding:5%;
    align-items: center;
    .input{
      width:70%;
      border:1px solid #eee;
      padding:10px;
    }
    .se{
      margin-left:30px;
      padding:10px 30px;
      background: #15BDDB;
      color:#fff;
    }
  }
  .item{
    padding:25px 30px;
    border-bottom:1px solid #eee;
    display: flex;
    align-items: center;
    /deep/ .van-icon{
      margin-right:15px;
    } 
  }
  .submit{
    position: fixed;
    bottom:0;
    // z-index: 1;
    // text-align: center;
    padding:15px 0;
    background: #fff;
    width:100%;
    // border-top:1px solid #eee;
    .btn{
      font-size: 30px;
      width:70%;
      background: #15BDDB;
      color:#fff;
      border-radius: 50px;
      padding:20px 0;
    }
  }
#taocan /deep/ .van-radio__label{
  color:#333;
}
#taocan /deep/ .van-radio{
  margin-bottom: 10px;
  margin-left: 30px;
}

/* 头部单选 */
#nav /deep/ .van-radio-group{
  display: flex;
  justify-content: space-around;
}
</style>



